import React from 'react';
import { CodeIcon } from '@heroicons/react/solid';
import { projects } from '../portfolio/projects';
const Projects = () => {
  return (
    <section id='projects' className='text-gray-400 bg-gray-900 body-font'>
      <div className='container px-5 py-10 mx-auto text-center lg:px-40 md:w-4/5'>
        <div className='flex flex-col w-full mb-20'>
          <CodeIcon className='mx-auto inline-block w-10 mb-4' />
          <h1 className='sm:text-4xl text-3xl font-medium title-font mb-4 text-white'>
            Apps I've Built
          </h1>
          <p className='lg:w-2/3 mx-auto leading-relaxed text-base'>
            Check out some of the projects that I have built during my journey
            as a web developer. These apps were made using ReactJS, ExpressJS,
            and other technologies.
          </p>
        </div>
        <div className='flex flex-wrap -m-4'>
          {projects
            .sort((a, b) => b._id - a._id)
            .map((project) => (
              <a
                target='_blank'
                href={project.link}
                key={project.image}
                className='sm:w-1/2 w-100 p-4'
                rel='noreferrer'
              >
                <div className='flex relative'>
                  <img
                    style={{ height: '230px' }}
                    alt='gallery'
                    className='absolute inset-0 w-full h-full object-cover object-center'
                    src={project.image}
                  />
                  <div
                    style={{ height: '230px' }}
                    className='px-8 py-10 relative z-10 w-full border-4 border-gray-800 bg-gray-900 opacity-0 hover:opacity-100'
                  >
                    <h2 className='tracking-widest text-sm title-font font-medium text-green-400 mb-1'>
                      {project.subtitle}
                    </h2>
                    <h1 className='title-font text-lg font-medium text-white mb-3'>
                      {project.title}
                    </h1>
                    <p className='leading-relaxed'>{project.description}</p>
                  </div>
                </div>
              </a>
            ))}
        </div>
      </div>
    </section>
  );
};

export default Projects;
